import { async } from 'regenerator-runtime';

import { TimeOutSec } from './config.js';

const timeout = function (s) {
  return new Promise(function (_, reject) {
    setTimeout(function () {
      reject(new Error(`Request took too long! Timeout after ${s} second`));
    }, s * 1000);
  });
};
export const AJAX = async function (url, uplaodData = undefined) {
  try {
    const fetchPro = uplaodData
      ? fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(uplaodData),
        })
      : fetch(url);

    const res = await Promise.race([fetchPro, timeout(TimeOutSec)]);

    const data = await res.json();
    if (!res.ok) throw new Error(`${data.message} (${res.status})`);

    return data;
  } catch (err) {
    throw err;
  }
};
